	transition: background-color 0.2s linear;
}

.media-meta-day::before {
	margin-right: 16px;
}

.media-meta-day::before,
.media-meta-day::after {
	content: '';
	-webkit-box-flex: 1;
	flex: 1 1;
	border-top: 1px solid #ebebeb;
}

.media-meta-day::after {
	content: '';
	-webkit-box-flex: 1;
	flex: 1 1;
	border-top: 1px solid #ebebeb;
}

.media-meta-day::after {
	margin-left: 16px;
}

